<template>
  <div class="twelve wide column page-body">
    <div class="header">
      <h2 class="ui header">Archives</h2>
      <div class="ui section divider"></div>
    </div>
    <div class="content">
      <template v-for="(data, k) in archives">
        <a v-bind:key="k" v-bind:class="lableColor(data.cat)" class="ui label" v-bind:href="'#'+data.cat" @click="scrollTo">
          {{ data.cat }}
        </a>
      </template>
    </div>
    <template v-for="(data, k) in archives">
      <div v-bind:key="'divider_'+ k" class="ui section divider"></div>
      <div v-bind:key="k" class="content">
        <a v-bind:name="data.cat" class="anchor" v-bind:href="'#'+data.cat"></a>
        <label v-bind:class="lableColor(data.cat)" class="ui label">{{ data.cat }}</label>
        <div class="ui ordered list">
          <template v-for="(lidata, dk) in data.data">
            <a v-bind:key="dk" class="item">
              {{ lidata.title }}
            </a>
          </template>
        </div>
      </div>
    </template>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
        <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Archives',
  computed: mapGetters({
    archives: 'blog/getArchives',
  }),
  methods: {
    scrollTo(evt) {
      location.hash = evt.srcElement.hash;
    },
    lableColor(cat) {
      return this.$common.findLabel(this.$const, cat);
    },
  },
};
</script>

<style scoped>
</style>



